.tooltip {
  padding: 5px;
  background: #4c4c4c;
  border-radius: 5px;
  position: absolute;
  top: 32px;
  right: -42px;
  z-index: 10;
  display: none;
  color: #fff;
  font-size: 12px;
  min-width: 110px;
  text-align: center;
}

.tooltip::before {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  background: #4c4c4c;
  position: absolute;
  top: -5px;
  left: 43px;
  z-index: 10;
}

.tooltip-hover:hover .tooltip {
  display: block;
}

