 overflow: hidden
.posr
  position: relative
.tooltip
  padding: 5px
  background: #4c4c4c
  border-radius: 5px
  position: absolute
  top: 32px
  right: -42px
  z-index: 10
  display: none
  color: #fff
  font-size: 12px
  min-width: 110px
  text-align: center
  &::before
    content: ''
    display: block
    width: 10px
    height: 10px
    -webkit-transform: rotate(45deg)
    transform: rotate(45deg)
    background: #4c4c4c
    position: absolute
    top: -5px
    left: 43px
    z-index: 10
  &-hover
    &:hover
      .tooltip
        display: block
.default-title
  font-size: 24px
  color: #212121
  font-weight: bold
  text-align: center
  text-transform: uppercase
  margin-top: 50px
  margin-bottom: 35px
  &--left
    display: flex
    align-items: center
    justify-content: flex-start
  &--line
    &::after
      display: block
      content: ''
      margin-left: 10px
      height: 1px
      width: 70%
      background: #dfdfdf
